/* =======================
   GENERAL STYLES
======================= */
body {
  margin: 0;
  font-family: 'Verdana', sans-serif;
  background-color: black;
  color: #d9d9d9;
}

a {
  text-decoration: none;
  color: #58e0c5;
}

/* =======================
   CONSISTENT SECTION HEADERS
======================= */
section h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 30px;
  text-align: center;
}

/* =======================
   NAVIGATION BAR
======================= */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 50px;
  background-color: #0f0f0f;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-links li a {
  color: #d9d9d9;
  font-size: 1.1rem;
  transition: color 0.3s;
}

.nav-links li a:hover {
  color: #c1eae1;
}

.cta-button {
  background-color: #58e0c5;
  color: black;
  padding: 10px 20px;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 0.3s;
}

.cta-button:hover {
  background-color: #45b89a;
}

/* =======================
   HERO SECTION
======================= */
.hero-section { 
  display: flex; 
  justify-content: center; 
  align-items: center; 
  flex-direction: column; 
  padding: 50px 20px; 
  background: linear-gradient(to bottom, black, rgba(119, 252, 225, 0.56));
   /* Gradient from black to light greenish */ 
   text-align: center; color: white; } 

   .hero-content h1 {
  color: white;
  font-size: 40px;
}

  .hero-summary {
  max-width: 720px;          /* wider container */
  margin-top: 25px;
  font-size: 1.05rem;        /* smaller than headers */
  line-height: 1.7;          /* more breathing room */
  color: #e6f4f1;            /* softer white */
}
  .hero-image { position: relative; /* Needed for the glowing border */ 
    width: 400px; 
    height: 400px; 
    margin-bottom: 20px; } /* Profile Picture Styling */ 
    
  .hero-image img { 
    width: 100%; 
    height: 100%; 
    clip-path: polygon( 50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25% ); 
    object-fit: cover; z-index: 1; /* Keeps the image above the glowing border */ 
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.5); } /* Glowing Border Behind the Picture */ 
    
  .hero-image::before { 
    content: ''; 
    position: absolute; 
    top: -10px; /* Adjust for border spacing */ 
    left: -10px; /* Adjust for border spacing */ 
    width: calc(100% + 20px); /* Adjust for border size */ 
    height: calc(100% + 20px); /* Adjust for border size */ 
    clip-path: polygon( 50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25% ); /* Matches hexagonal shape */ 
    background: linear-gradient(135deg, #f9f9f9, #f5f6f8, #f2f5f4); /* Gradient glow effect */ 
    filter: blur(8px); /* Creates the glowing effect */ z-index: 0; /* Places the glow behind the image */ 
    animation: glowingHexagon 3s infinite; /* Animation for glowing effect */ } /* Glowing Animation */ 
    
    @keyframes glowingHexagon { 0% { 
      opacity: 0.8; 
      box-shadow: 0 0 20px rgb(209, 220, 218), 0 0 40px rgba(193, 225, 219, 0.4); } 50% 
      { opacity: 1; 
        box-shadow: 0 0 40px rgb(72, 82, 80), 0 0 60px rgba(165, 190, 185, 0.8); } 100% 
      { opacity: 0.8; box-shadow: 0 0 20px rgba(119, 128, 126, 0.6), 0 0 40px rgba(88, 224, 197, 0.4); } }

/* =======================
   EXPERIENCE SECTION
======================= */
.experience-section {
  padding: 50px;
  background-color: #0f0f0f;
  text-align: center;
}

.experience-section h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

/* =======================
   SKILLS SECTION
======================= */
.skills-section {
  padding: 50px;
  background: linear-gradient(to bottom, black, rgba(119, 252, 225, 0.56));
  text-align: center;
}

.skills-section h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
}

.skill-item {
  width: 130px;
  height: 140px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  background-color: rgba(255, 255, 255, 0.77);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s, background-color 0.3s;
}

.skill-item:hover {
  transform: scale(1.05);
  background-color: #f0f0f0;
}

.skill-item img {
  width: 50px;
  height: 50px;
}

.skill-item span {
  color: black;
  font-size: 0.9rem;
  font-weight: bold;
  text-transform: uppercase;
}

/* =======================
   PROJECTS + LEADERSHIP
======================= */
.projects-section {
  padding: 50px 20px;
  background-color: black;
  text-align: center;
}

.projects-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.project-card {
  background-color: #1a1a1a;
  padding: 20px;
  width: 300px;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s;
}

.project-card:hover {
  transform: scale(1.05);
}

.project-card h3 {
  color: #58e0c5;
}

.project-card strong {
  color: #58e0c5;
}

/* =======================
   CONTACT SECTION
======================= */
.contact-section {
  padding: 50px;
  background: linear-gradient(to top, rgba(119, 252, 225, 0.56), black);
  text-align: center;
}

.contact-details a {
  color: white;
  font-weight: bold;
}

.contact-details a:hover {
  color: #58e0c5;
}

.social-links {
  margin-top: 20px;
  display: flex;
  gap: 20px;
  justify-content: center;
}

.social-links a {
  background: white;
  color: black;
  padding: 10px;
  border-radius: 50%;
  transition: transform 0.3s;
}

.social-links a:hover {
  transform: scale(1.2);
}

/* =======================
   FOOTER
======================= */
.footer {
  text-align: center;
  padding: 20px 0;
  font-size: 0.9rem;
  color: #d9d9d9;
  background-color: black;
}

/* =======================
   EDUCATION & CERTIFICATIONS
======================= */
.education-section {
  padding: 50px 20px;
  background-color: black;
  text-align: center;
}

.education-container {
  display: flex;
  gap: 30px;
  justify-content: center;
  align-items: stretch;
  flex-wrap: wrap;
}

/* Make cards equal width on desktop */
.education-container .project-card {
  width: 360px;
}

/* Certifications */
.cert-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-top: 10px;
  text-align: left;
}

.cert-item {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 0.95rem;
}

.cert-item svg {
  color: #58e0c5;
  min-width: 20px;
}
